<template lang="html">
  <div class="header">
    <h1>Formular za ocenjivanje kompetencija - Konacna Procena</h1>
    <h3>(1=Ne Zadovoljava Ocekivanja)</h3>
    <h3>(2=Potrebno Poboljsanje)</h3>
    <h3>(3=Zadovoljava Ocekivanja)</h3>
    <h3>(4=Iznad Ocekivanja)</h3>
  </div>

  <div class="form-group">
    <label for="collegue">Podredjeni za koga se vrsi konacna procena:</label>
    <select name="collegue" id="collegue" v-model="podredjeniName" @change="onChangePodredjeni($event)">
       <option  v-for="podr in podredjeni" v-bind:key="podr.name">{{podr.name}}</option>
    </select>
    </div>

  <div class="kompetence">
<h3>Kompetence</h3>
    <table id="kompetenceTable">
      <tr>
        <th>Kompetence :</th>
        <th>Ponder</th>
        <th>Samoprocena</th>
        <th>Konacna Procena</th>
        <th>Rangiranje</th>
      </tr>

      <tr v-for="(komp,i) in kompetence"  v-bind:key="i">
        <td>{{komp.name}}</td>
        <td>{{komp.ponder}}%</td>
        <td>{{komp.samoProcena}}</td>
        <td><select name="type" id="type" v-model="komp.konacnaProcena" @change="updateTable">
           <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
        </select></td>
        <td>{{komp.rangiranje}}</td>
      </tr>
    </table>
</div>


<div class="licni">
  <h3>Licni KPI</h3>
  <table id="licniTable">
    <tr>
      <th>Kompetence :</th>
      <th>Ponder</th>
      <th>Samoprocena</th>
      <th>Konacna Procena</th>
      <th>Rangiranje</th>
    </tr>

    <tr v-for="(licni,j) in licniKpi"  v-bind:key="j">
      <td>{{licni.name}}</td>
      <td>{{licni.ponder}}%</td>
      <td>{{licni.samoProcena}}</td>
      <td><select name="type" id="type" v-model="licni.konacnaProcena" @change="updateTable">
         <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
      </select></td>
      <td>{{licni.rangiranje}}</td>
    </tr>

  </table>

</div>

<div class="kompanijski">
  <h3>Kompanijski KPI</h3>
  <table id="kompanijskiTable">
    <tr>
      <th>Kompetence :</th>
      <th>Ponder</th>
      <th>Samoprocena</th>
      <th>Konacna Procena</th>
      <th>Rangiranje</th>
    </tr>

    <tr v-for="(kompa,k) in kompanijskiKpi"  v-bind:key="k">
      <td>{{kompa.name}}</td>
      <td>{{kompa.ponder}}%</td>
      <td>{{kompa.samoProcena}}</td>
      <td><select name="type" id="type" v-model="kompa.konacnaProcena" @change="updateTable">
         <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
      </select></td>
      <td>{{kompa.rangiranje}}</td>
    </tr>
  </table>
</div>



  <div class="rezultat1" v-if="ukupno<0.75">
    <h3>Ukupno: {{ukupno}}</h3>
    <p>Rezultati loši, potrebno unapređenje za vršenje trenutne uloge ili prelazak na drugo radno mesto	</p>
  </div>
  <div class="rezultat2" v-if="ukupno>=0.75 && ukupno< 0.99">
    <h3>Ukupno: {{ukupno}}</h3>
    <p>Rezultati dobri, ali  neophodan razvoj za vršenje trenutne uloge
</p>
  </div>
  <div class="rezultat3" v-if="ukupno>=0.99 && ukupno< 1.17">
    <h3>Ukupno: {{ukupno}}</h3>
    <p>Apsolutno ostvarenje rezultata u trenutnoj ulozi
</p>

  </div>
  <div class="rezultat4" v-if="ukupno>=1.17">
    <h3>Ukupno: {{ukupno}}</h3>
    <p>Prebačaj rezultata u trenutnoj ulozi
</p>

  </div>
  <div class="komentar">
    <h4>Komentar</h4>
    <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentar"
      id="komentar"
      >
  </div>

  <button type="button" name="button" @click="submit">Submit</button>

</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';

export default {
  computed: mapGetters(['user','users','companys','usersSameCompany']),
  data(){
    return{
      odgovori:[1,2,3,4],
      komentar:"",
      kompetence:[],
      licniKpi:[],
      kompanijskiKpi:[],
      ukupno:0,
      evaluatedUser:'',
      podredjeni:[],
      podredjeniName:'',
      podredjeniCeo:'',
    }
  },
  methods:{
    ...mapActions(['getProfile','getCompanies','getCollegues','setKpiKonacnaProcena']),
    onChangePodredjeni(event){
      for (var i = 0; i < this.usersSameCompany.length; i++) {
        if(this.usersSameCompany[i].name == event.target.value){
          this.podredjeniCeo=this.usersSameCompany[i];
        }
      }

      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation.kompetence.length; i++) {
        this.kompetence[i].samoProcena=this.podredjeniCeo.performanceEvaluation.kompetence[i].samoProcena;
        this.kompetence[i].konacnaProcena=this.podredjeniCeo.performanceEvaluation.kompetence[i].konacnaProcena;
        this.kompetence[i].rangiranje=this.podredjeniCeo.performanceEvaluation.kompetence[i].rangiranje;
      }
      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation.kompanijski.length; i++) {
        this.kompanijskiKpi[i].samoProcena=this.podredjeniCeo.performanceEvaluation.kompanijski[i].samoProcena;
        this.kompanijskiKpi[i].konacnaProcena=this.podredjeniCeo.performanceEvaluation.kompanijski[i].konacnaProcena;
        this.kompanijskiKpi[i].rangiranje=this.podredjeniCeo.performanceEvaluation.kompanijski[i].rangiranje;
      }
      this.licniKpi=[];
      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation.licniKpi.length; i++) {
              this.licniKpi.push(this.podredjeniCeo.performanceEvaluation.licniKpi[i]);
      }
      this.updateTable();
    },
    updateTable(){
      //kompetence
      let ukupnoKompetence = 0;
      for (var i = 0; i < this.kompetence.length; i++) {
        if(this.kompetence[i].konacnaProcena!=null){
          this.kompetence[i].rangiranje=((this.kompetence[i].konacnaProcena*0.25 + 0.25)*this.kompetence[i].ponder)/100;
          ukupnoKompetence+=this.kompetence[i].rangiranje;
        }
      }
      //licni
      let ukupnoLicni=0;
      for ( i = 0; i < this.licniKpi.length; i++) {
        if(this.licniKpi[i].konacnaProcena!=null){
          this.licniKpi[i].rangiranje=((this.licniKpi[i].konacnaProcena*0.25 + 0.25)*this.licniKpi[i].ponder)/100;
          ukupnoLicni+=this.licniKpi[i].rangiranje;
        }
      }
      //kompanijski
      let ukupnoKompanijski=0;
      for ( i = 0; i < this.kompanijskiKpi.length; i++) {
        if(this.kompanijskiKpi[i].konacnaProcena!=null){
          this.kompanijskiKpi[i].rangiranje=((this.kompanijskiKpi[i].konacnaProcena*0.25 + 0.25)*this.kompanijskiKpi[i].ponder)/100;
          ukupnoKompanijski+= this.kompanijskiKpi[i].rangiranje;
        }
      }
      this.ukupno = 0.2 * ukupnoKompetence + 0.5 * ukupnoLicni + 0.3*ukupnoKompanijski;

    },

    submit(){
      let kpiData={
        licniKpi:this.licniKpi,
        kompanijski:this.kompanijskiKpi,
        kompetence:this.kompetence,
        userId:this.podredjeniCeo._id
      };
      this.setKpiKonacnaProcena(kpiData);
    },
  },
  async created(){

    await this.getProfile();
    await this.getCompanies();
    await this.getCollegues(this.user.type);

    let thisCompany;
    for (var i = 0; i < this.companys.length; i++) {
      if(this.user.type==this.companys[i].name){
        thisCompany=this.companys[i];
      }
    }

    for ( i = 0; i < thisCompany.performanceEvaluation.kompetence.length; i++) {
      this.kompetence.push(thisCompany.performanceEvaluation.kompetence[i]);
    }

    for ( i = 0; i < thisCompany.performanceEvaluation.kompanijski.length; i++) {
      this.kompanijskiKpi.push(thisCompany.performanceEvaluation.kompanijski[i]);
    }

    for ( i = 0; i < this.user.performanceEvaluationPodredjeni.length; i++) {
      for (var j = 0; j < this.usersSameCompany.length; j++) {
        if(this.user.performanceEvaluationPodredjeni[i]==this.usersSameCompany[j]._id){
          this.podredjeni.push(this.usersSameCompany[j]);
        }
      }
    }

  },

  }

</script>

